@import "admin/templates/optins/_tpl-locate_form.scss";
@import "admin/templates/optins/_tpl-locate_image.scss";

%structure-edit {
	#wph-optin--structure {
		border-bottom: 1px solid $color--lilac;
		> div:first-child {
			position: relative;
			@extend %left-column;
		}
		> div:last-child {
			@extend %right-column;
		}
		@include screen(medium) {
			margin-bottom: 25px;
			padding-bottom: 29px;
		}
	}
	@extend %form-location;
	@extend %image-location;
	#optin_optional_elements {
		margin: -1px 0 0;
		#wph-optin--fname,
		#wph-optin--lname {
			display: inline-block;
			.wph-input--checkbox {
				margin: 0 7px 0 2px;
			}
			.wph-input--checkbox + label {
				display: inline-block;
				margin: -3px 0 0;
				letter-spacing: 0px;
			}
		}
		#wph-optin--fname {
			margin-right: 26px;
		}
	}
	#wpoi-success-message-fields {
		border-bottom: 1px solid $color--lilac;
		> div:first-child {
			position: relative;
			@extend %left-column;
		}
		> div:last-child {
			@extend %right-column;
		}
		@include screen(medium) {
			margin-bottom: 25px;
			padding-bottom: 29px;
		}
	}
}