#wpoi-box-dashboard-optins {
	.box-content h6 {
		margin: 30px 0 10px;
		letter-spacing: 0;
		text-align: center;
	}
	.wph-dashboard--steps {
		margin-bottom: 47px;
		tbody tr {
			border-bottom: 1px solid $color--lilac;
			td {
				padding: 20px 0 10px;
				.wph-table--title,
				.wph-table--subtitle {
					letter-spacing: 0;
					text-align: center;
					@include screen(small) {
						max-width: 323px;
						text-align: left;
					}
				}
			}
			&:first-child td {
				border-top: 0;
			}
			td.wph-dashstep--icon {
				width: 74px;
				display: none;
				.wph-icon {
					margin-right: 10px;
					color: $color--steel;
				}
				.i-optin {
					height: 65px;
					font-size: 40px;
					margin-left: 7px;
					&:before {
						line-height: 65px;
					}
				}
				.i-magic {
					height: 64px;
					font-size: 54px;
					margin-left: 8px;
					&:before {
						line-height: 64px;
					}
				}
				@include screen(small) {
					display: table-cell;
				}
			}
			td.wph-dashstep--create {
				padding: 20px 0 30px;
				@include screen(small) {
					width: 70px;
				}
			}
			&:last-child td {
				@include screen(small) {
					border-bottom: 1px solid $color--lilac;
				}
			}
			@include screen(small) {
				border-bottom: 0;
			}
		}
	}
}