.hustle-two .wph-accordions {
	.wph-accordions--item {
		margin-bottom: 10px;
		header {
			overflow: hidden;
			position: relative;
			margin-bottom: 1px;
			padding: 0 30px;
			cursor: pointer;
			background: $color--white;
			.toggle {
				top: -1px;
				margin-right: 8px;
			}
			.wph-label--module,
			.wph-label--provider {
				display: inline-block;
			}
			.wph-label--module {
				margin: 0 6px 0 0;
				padding: 0;
				color: $color--shaft;
				font: 700 14px/70px $font--alt;
				letter-spacing: -0.15px;
			}
			.wph-label--provider {
				margin-left: 5px;
				opacity: 0.4;
				margin: 0;
				padding: 0;
				color: $color--shaft;
				font: 400 14px/70px $font;
				letter-spacing: -0.15px;
			}
			.wph-accordion--buttons {
				height: 30px;
				display: inline-block;
				position: relative;
				vertical-align: middle;
				margin-left: 10px;
				transition: 0.2s linear;
				.wph-button {
					width: 40px;
					height: 30px;
					overflow: hidden;
					margin-right: 8px;
					padding: 0 0 0 1px;
					letter-spacing: 0;
					&:before {
						display: block;
						font-family: $font--icon;
						@include screen(small) {
							display: none;
						}
					}
					&:last-child {
						margin-right: 0;
					}
					@include screen(small) {
						width: auto;
						padding: 0 14px;
					}
				}
				.wph-button--gray:before {
					content: $i-edit;
				}
				.wph-button--delete:before {
					content: $i-trash;
				}
				.hustle-delete-module-confirm:before {
					content: $i-check;
				}
				.hustle-delete-module-cancel:before {
					content: $i-close;
				}
				@include screen(small) {
					opacity: 0;
					margin-left: 27px;
				}
			}
			.button-view-email-list {
				width: 40px;
				height: 30px;
				overflow: hidden;
				padding: 0;
				&:before {
					content: $i-mail;
					display: block;
					font-family: $font--icon;
					@include screen(small) {
						display: none;
					}
				}
				@include screen(small) {
					width: auto;
					position: absolute;
					top: 20px;
					right: 71px;
					padding: 0 15px 0 14px;
				}
			}
			.accordion-state {
				cursor: pointer;
				position: absolute;
				top: 26px;
				right: 29px;
				font-size: 12px;
			}
			.hustle-delete-module-confirmation {
				float: right;
				display: inline-block;
				margin-left: 5px;
				label {
					float: left;
					display: none;
					margin: 0 5px 0 0;
					line-height: 30px;
					@include screen(small) {
						display: inline-block;
					}
				}
				@include screen(small) {
					margin-left: 20px;
				}
			}
			&:hover {
				.wph-accordion--buttons {
					@include screen(small) {
						opacity: 1;
					}
				}
			}
			@include screen(small) {
				height: 70px;
			}
		}
		section {
			display: none;
			position: relative;
			background: $color--haze;
			transform-style: preserve-3d;
			.wph-accordion--disable {
				width: 100%;
				height: 100%;
				position: absolute;
				z-index: 9;
				background: $color--haze;
				transform-style: preserve-3d;
				p {
					display: block;
					position: absolute;
					top: 50%;
					right: 0;
					left: 0;
					padding: 0 30px;
					transform: translateY(-50%);
					text-align: center;
				}
			}
		}
		&.wph-accordion--closed {
			margin-bottom: 2px;
			header {
				.accordion-state {
					top: 30px;
					.i-arrow {
						display: block;
						transform: rotate(180deg);
					}
				}
			}
		}
		&.wph-accordion--open {
			section {
				display: block;
			}
		}
		&:last-child {
			margin-bottom: 0;
		}
	}
	.wph-table {
		thead tr th,
		tbody tr td {
			&.wph-module--display {
				width: 318px;
			}
			&.wph-module--views {
				width: 67px;
			}
			&.wph-module--conversions {
				width: 82px;
			}
			&.wph-module--rates {
				width: 105px;
			}
			&.wph-module--admin {
				width: 90px;
			}
			&.wph-module--active {
				width: 115px;
			}
		}
		thead {
			background: $color--alabaster;
			tr th {
				padding: 0 5px;
				background: $color--alabaster;
				border-bottom: 1px solid $color--concrete;
				color: $color--dark;
				font: 400 12px/30px $font;
				letter-spacing: -0.17px;
				text-transform: none;
				&.wph-module--views,
				&.wph-module--conversions,
				&.wph-module--rates,
				&.wph-module--admin,
				&.wph-module--active {
					text-align: center;
				}
				&:first-child {
					padding-left: 30px;
				}
				&:last-child {
					padding-right: 30px;
				}
			}
		}
		tbody {
			tr {
				margin: 0;
				border-bottom: 1px solid $color--lilac;
				&:last-child {
					th, td {
						border-bottom: 0;
						@include screen(small) {
							border-bottom: 0;
						}
					}
				}
				&:hover {
					th, td {
						background: $color--white;
					}
					.wph-module--edit {
						opacity: 1;
						left: 4px;
					}
				}
				@include screen(small) {
					border-bottom: 0;
				}
			}
			tr th {
				padding: 15px 30px 0;
				border: 0;
				font: 700 12px/26px $font--alt;
				letter-spacing: -0.17px;
				transition: 0.2s ease;
				span {
					position: relative;
					padding-left: 23px;
					&:before {
						content: " ";
						width: 18px;
						height: 18px;
						display: block;
						position: absolute;
						top: -2px;
						left: 0;
						margin-right: 10px;
						@extend %sprite;
					}
				}
				@include screen(small) {
					padding: 0 10px 0 30px;
					border-bottom: 1px solid $color--concrete;
					line-height: 66px;
				}
			}
			tr:nth-child(1) th span:before {
				background-position: -112px -54px;
			}
			tr:nth-child(2) th span:before {
				background-position: 0 -54px;
			}
			tr:nth-child(3) th span:before {
				background-position: -28px -54px;
			}
			tr:nth-child(4) th span:before {
				background-position: -56px -54px;
			}
			tr:nth-child(5) th span:before {
				background-position: -84px -54px;
			}
			tr td {
				position: relative;
				padding: 0 30px 15px;
				color: $color--shuttle;
				font: 400 12px/14px $font;
				transform-style: preserve-3d;
				transition: 0.2s ease;
				&.wph-module--display {
					padding: 0 30px 15px;
					white-space: normal;
					@include screen(small) {
						padding: 0 5px;
					}
				}
				&.wph-module--views,
				&.wph-module--conversions,
				&.wph-module--rates {
					&:before {
						content: attr(data-title) ":";
						margin-right: 4px;
						font-weight: bold;
						@include screen(small) {
							display: none;
						}
					}
					@include screen(small) {
						text-align: center;
					}
				}
				&.wph-module--admin,
				&.wph-module--active {
					margin: 0;
					&:before {
						content: attr(data-title) ":";
						margin-right: 4px;
						color: $color--shuttle;
						font: 700 12px/14px $font;
						text-transform: none;
						@include screen(small) {
							display: none;
						}
					}
				}
				&.wph-module--active {
					@include screen(small) {
						padding-right: 30px;
					}
				}
				.wph-module--edit {
					opacity: 0;
					display: none;
					position: absolute;
					top: 50%;
					left: -55px;
					transform: translate(0,-50%);
					transition: opacity 0.2s linear, left 0.3s ease-out;
					background: $color--white;
					box-shadow: 0 0 20px 15px rgba(255,255,255,.8);
					&:hover {
						background: $color--jumbo;
						color: $color--white;
					}
					@include screen(small) {
						display: block;
					}
				}
				@include screen(small) {
					padding: 0 5px;
					border-bottom: 1px solid $color--concrete;
				}
			}
		}
	}
}

#hustle-cc-listings {
	.wph-table tbody {
		tr:nth-child(1) th span:before {
			background-position: 0 -54px;
		}
		tr:nth-child(2) th span:before {
			background-position: -28px -54px;
		}
	}
}





