.hustle-two .tabs {
	.tabs-header {
		width: auto;
		display: flex;
		flex-wrap: wrap;
		flex-direction: row;
		margin: 20px 0;
		li {
			width: 100%;
			cursor: pointer;
			display: block;
			margin: 0;
			padding: 0 15px;
			border: 1px solid $border--crystal;
			border-bottom: 0;
			background: $color--haze;
			&:first-child {
				border-radius: 5px 5px 0 0;
				@include screen(small) {
					border-left: 1px solid $border--crystal;
					border-radius: 10px 0 0 10px;
				}
			}
			&:last-child {
				border-bottom: 1px solid $border--crystal;
				border-radius: 0 0 5px 5px;
				@include screen(small) {
					border-radius: 0 10px 10px 0;
				}
			}
			label {
				overflow: hidden;
				cursor: pointer;
				display: block;
				margin: 0;
				padding: 10px 5px;
				border-radius: inherit;
				color: $color--tangaroa;
				font: 600 11px/14px $font--opn;
				text-overflow: ellipsis;
				white-space: nowrap;
				@include screen(small) {
					padding: 7px 5px;
					text-align: center;
				}
			}
			&:hover,
			&.current {
				border-color: $color--manatee;
				background: $color--manatee;
				label {
					color: $color--white;
				}
			}
			@include screen(small) {
				width: auto;
				min-width: 140px;
				display: inline-block;
				border-bottom: 1px solid $border--crystal;
				border-left: 0;
			}
		}
		input {
			display: none;
		}
		@include screen(medium) {
			margin: 30px 0;
		}
	}
	.tabs-body {
		.tabs-content {
			display: none;
			&.current {
				display: block;
			}
		}
	}
}

.hustle-two .tabs-fill {
	.tabs-header {
		li {
			flex: 1;
		}
	}
}