%settings-modules {
	#modules-activity {
		.wph-table {
			margin: 20px 0 15px;
			thead tr th,
			tbody tr td {
				&.wph-module--loginUser {
					@include screen(small) {
						width: 124px;
					}
				}
				&.wph-module--admin {
					@include screen(small) {
						width: 70px;
					}
				}
			}
			thead {
				tr th {
					line-height: 28px;
					&:not(:first-child) {
						text-align: center;
					}
				}
			}
			tbody {
				tr td {
					line-height: 24px;
					small {
						font-weight: bold;
					}
					@include screen(small) {
						padding: 16px 0 15px;
					}
				}
				tr td.wph-module--name {
					letter-spacing: 0.5px;
					@include screen(small) {
						padding-right: 12px;
					}
				}
			}
			@include screen(small) {
				margin: 26px 0 15px;
			}
		}
	}
}