.hustle-two .box {
	margin: 0 0 30px;
	border-radius: 5px;
	background-color: $color--white;
	box-shadow: 0 2px 0 $color--lily;
	.box-title {
		position: relative;
		margin-bottom: -1px;
		padding: 0 20px;
		border-bottom: 1px solid $color--lily;
		h3 {
			overflow: hidden;
			display: block;
			margin: 0;
			padding-right: 30px;
			text-overflow: ellipsis;
			white-space: nowrap;
		}
		.wph-icon {
			position: absolute;
			top: 22px;
			right: 30px;
			font-size: 12px;
			color: $color--sand;
			cursor: pointer;
			transition: 0.25s ease;
			&:hover {
				color: $color--montana;
			}
		}
		@include screen(medium) {
			padding: 0 30px;
		}
	}
	.box-content {
		padding: 0 5px;
		@include screen(medium) {
			padding: 0 15px;
		}
	}
	&:last-child {
		margin-bottom: 0;
	}
}