#wph-modules--overview {
	.tabs {
		.tabs-header {
			margin: 30px 0 0;
			border-radius: 10px;
		}
		.tabs-header li {
		}
	}
	.wph-modules-overview {
		display: none;
		&.current {
			display: block;
		}
	}
	.wph-table {
		&:first-child {
			margin-bottom: 21px;
		}
		thead {
			display: block;
			tr, tr th {
				display: block;
			}
			tr th {
				margin-bottom: 10px;
				border-bottom: 1px solid $color--lilac;
				line-height: 30px;
				@include screen(small) {
					margin-bottom: 0;
					border-bottom: 0;
				}
			}
		}
		tbody {
			tr:not(:last-child) {
				margin-bottom: 15px;
				@include screen(small) {
					margin-bottom: 0;
				}
			}
			tr td {
				@include screen(small) {
					padding-bottom: 12px;
				}
			}
			tr td.wph-module--edit {
				.wph-button {
					@include screen(medium) {
						opacity: 0;
						visibility: hidden;
					}
				}
				@include screen(small) {
					width: 55px;
				}
			}
			tr:hover {
				td.wph-module--edit .wph-button {
					@include screen(medium) {
						opacity: 1;
						visibility: visible;
					}
				}
			}
		}
		&:last-child {
			margin-bottom: 30px;
		}
	}
}