%form-location {
	#optin_form_location {
		margin-bottom: 28px;
	}
	.wpoi-form-design-wrap {
		display: block;
		.wpoi-form-design {
			display: inline-block;
			&:nth-child(3) {
				@include screen(medium) {
					margin-right: 56px;
				}
			}
			&:last-child {
				margin-right: 0;
			}
			@include screen(small) {
				margin-right: 20px;
			}
			@include screen(medium) {
				margin-right: 57px;
			}
		}
		.wpoi-form-design label:not(.wph-icon) {
			width: 61px;
			height: 36px;
			cursor: pointer;
			margin: 0 0 5px;
			@extend %sprite;
			&.wpoi-form-design-one {
				background-position: 1px 0;
			}
			&.wpoi-form-design-two {
				background-position: -92px 0;
			}
			&.wpoi-form-design-three {
				background-position: -183px 0;
			}
			&.wpoi-form-design-four {
				background-position: -274px 0;
			}
		}
		.wpoi-form-design .wph-input--radio {
			width: 20px;
			height: 20px;
			display: block;
			margin: 0 auto;
			input + label {
				width: 20px;
				height: 20px;
				&:before {
					line-height: 20px;
				}
			}
		}
	}
}