.hustle-two .block {
	position: relative;
	label {
		cursor: default;
		margin: 20px 0 10px;
	}
	.wph-label--notice,
	.wph-label--loading {
		margin: 0;
	}
	.wph-label--radio,
	.wph-label--checkbox {
		label {
			margin: 0;
		}
	}
	.block-notification {
		position: relative;
		input[type="text"] {
			padding-right: 121px;
		}
		input[type="button"] {
			width: 111px;
			display: block;
			position: absolute;
			top: 0;
			right: 0;
			border-top-left-radius: 0;
			border-bottom-left-radius: 0;
			text-align: center;
		}
		&:last-child {
			margin-bottom: 15px;
		}
	}
}