%display-settings {
	#wph-optin--information,
	#wph-optin--after_content,
	#wph-optin--popup,
	#wph-optin--slidein {
		> div:first-child {
			position: relative;
			@extend %left-column;
		}
		> div:last-child {
			@extend %right-column;
		}
	}
	#wph-optin--after_content_conditions,
	#wph-optin--popup_conditions,
	#wph-optin--popup_triggers,
	#wph-optin--slidein_conditions {
		p {
			margin: 0 0 31px;
			letter-spacing: 0px;
		}
	}
	#wph-optin--after_content,
	#wph-optin--popup {
		padding-bottom: 25px;
	}
	#wph-optin--slidein {
		padding-bottom: 35px;
	}
	#wpoi-wizard-settings-widget-message {
		.row {
			margin-bottom: 10px;
			padding: 0 0 30px;
			border-bottom: 1px solid $color--lilac;
		}
		label {
			line-height: 20px;
			letter-spacing: 0px;
			&:nth-child(1) {
				margin-bottom: 20px;
			}
			&:nth-child(2) {
				margin-bottom: 14px;
			}
		}
	}
	#optin-afterc-animation-block {
		margin-top: 19px;
		.select2-container {
			width: 74% !important;
		}
	}
	#triggers-section-popup {
		
		#wpoi-triggers-popup-scroll {
			input[type="text"] {
				max-width: 380px;
				margin-left: 28px;
			}
		}
	}
	#wph-optin--popup_triggers,
	#wph-optin--slidein_triggers,
	#wph-optin--slidein_options,
	#wph-optin--expiricy {
		padding-bottom: 28px;
		border-bottom: 1px solid $color--lilac;
	}
	#wph-optin--popup_animation {
		padding-bottom: 30px;
		border-bottom: 1px solid $color--lilac;
		.row {
			padding: 0;
			.col-xs-12:first-child {
				@include screen(small) {
					padding-left: 0;
				}
			}
			.col-xs-12:last-child {
				@include screen(small) {
					padding-right: 0;
				}
			}
			label {
				cursor: text;
				margin-bottom: 9px;
			}
			.select2-container {
				width: 100% !important;
				max-width: 310px;
			}
		}
	}
	#wph-optin--popup_settings {
		#wph-optin--additional_settings {
			margin-bottom: 20px;
		}
		#wph-optin--expiricy {
			label {
				margin-bottom: 5px;
			}
			.wph-label--number {
				> label {
					left: 75px;
				}
				.wph-input--number {
					width: 70px;
				}
			}
		}
	}
}