%shape-edit {
	#wph-optin--shapes {
		border-bottom: 1px solid $color--lilac;
		> div:first-child {
			position: relative;
			@extend %left-column;
		}
		> div:last-child {
			@extend %right-column;
			.tabs .tabs-header {
				margin: 0;
				li {
					min-width: 130px;
				}
			}
		}
		@include screen(small) {
			margin-bottom: 15px;
			padding-bottom: 40px;
		}
	}
	#wph-shapes--edit {
		margin-top: 20px;
		margin-bottom: 39px;
		padding: 10px 15px 39px;
		border-radius: 5px;
		background: $color--storm;
		h4 {
			padding: 0 15px;
			line-height: 50px;
			&:nth-child(3) {
				margin-bottom: 4px;
			}
		}
		.wph-label--alt {
			margin: 0 0 5px;
			letter-spacing: 0px;
		}
		.row:nth-child(2) {
			margin-bottom: 10px;
			padding: 4px 0 0;
		}
		.row:nth-child(4) {
			padding: 0;
			.col-xs-12 {
				position: relative;
			}
			.wph-pickers {
				top: 6px;
			}
			label {
				position: absolute;
				top: 11px;
				left: 49px;
				margin: 0;
			}
		}
	}
}