%preview-open {
	.wph-preview--eye {
		width: 50px;
		height: 50px;
		cursor: pointer;
		display: block;
		position: absolute;
		top: 79px;
		left: 0;
		margin: 0;
		padding: 0;
		border: 1px solid $color--elephant;
		border-radius: 100%;
		background: $color--astronaut;
		text-align: center;
		transition: none;
		.wph-icon {
			position: relative;
			top: 1px;
			color: $color--white;
		}
		&:hover {
			background: $color--elephant;
		}
		// Sticky eye icon
		&.wph-sticky--element {
			position: fixed;
			top: 0;
			left: 220px;
			transform: translateY(92px);
			z-index: 99;
		}
	}
}