#wph-module-stats {
	.wph-module--graph {
		display: none;
		margin: 0 0 30px;
		canvas {
			margin: 0 auto;
		}
		@include screen(small) {
			display: block;
		}
		@include screen(medium) {
			max-width: 527px;
			margin: 30px auto;
		}
	}
	.wph-table {
		margin: 20px 0;
		thead tr th,
		tbody tr td {
			&:last-child {
				border-right: 0;
			}
			@include screen(small) {
				border-right: 1px solid $color--lilac;
			}
		}
		thead tr {
			th {
				border-right-color: transparent;
				border-bottom: 1px solid $color--lilac;
				line-height: 28px;
			}
			th:not(.wph-table--name) {
				width: 80px;
				text-align: center;
			}
		}
		tbody tr {
			margin-bottom: 15px;
			padding-bottom: 15px;
			border-bottom: 1px solid $color--lilac;
			td {
				[tooltip]:after {
					width: auto;
					padding: 5px 12px 4px;
				}
				@include screen(small) {
					padding: 18px 0;
					border-top: 1px solid $color--lilac;
				}
			}
			td.wph-table--name {
				margin-bottom: 10px;
				color: $color--shaft;
				font-size: 15px;
				line-height: 25px;
				font-family: $font--alt;
				font-weight: 700;
				text-transform: uppercase;
				i:not(.wph-icon) {
					width: 6px;
					height: 6px;
					display: inline-block;
					position: relative;
					top: -2px;
					margin-right: 10px;
					border-radius: 100%;
					&.wph-stats--yellow {
						background: $color--yellow;
					}
					&.wph-stats--green {
						background: $color--green;
					}
					&.wph-stats--blue {
						background: $color--maya;
					}
				}
				span.wpoi-tooltip {
					display: none;
					@include screen(small) {
						width: 22px;
						display: inline-block;
					}
				}
				.wph-icon {
					width: 22px;
					display: inline-block;
				}
				@include screen(small) {
					margin-bottom: 0;
					padding-right: 15px;
					color: inherit;
					line-height: 22.5px;
					font-family: $font;
					font-weight: 400;
					text-transform: none;
				}
			}
			td:not(.wph-table--name) {
				&:before {
					content: attr(data-title) ":";
					margin-right: 10px;
					color: $color--shaft;
					font: 700 15px/22.5px $font--alt;
					letter-spacing: -0.16px;
					text-transform: uppercase;
					@include screen(small) {
						display: none;
					}
				}
				&.c-current {
					@include screen(small) {
						color: $color--shaft;
						font-weight: 700;
					}
				}
				@include screen(small) {
					width: 80px;
					text-align: center;
				}
			}
			&:last-child {
				margin-bottom: 0;
				padding-bottom: 0;
				border-bottom: 0;
			}
			@include screen(small) {
				margin-bottom: 0;
				padding-bottom: 0;
				border-bottom: 0;
			}
		}
		@include screen(medium) {
			max-width: 498px;
			margin: 31px 0 30px 4px;
		}
	}
}