.hustle-two {
	h1, h2, h3, h4, h5, h6 {
		display: block;
		margin: 0;
		padding: 0;
		line-height: 1em;
		color: $color--montana;
	}
	h1, h2, h3, h4 {
		font-family: $font--alt;
	}
	h1, h2, h3, h4 {
		font-weight: 700;
		text-transform: uppercase;
	}
	h5, h6 {
		font-family: $font;
		font-weight: 400;
	}
	h1 {
		margin: 30px 0;
		color: $color--shaft;
		font-size: 30px;
		line-height: 40px;
		letter-spacing: -0.6px;
	}
	h2 {
		margin: 20px 0;
		color: $color--steel;
		font-size: 20px;
		line-height: 36px;
		letter-spacing: -0.5px;
		@include screen(small) {
			margin: 0;
			font-size: 22px;
			line-height: 60px;
		}
	}
	h3 {
		color: $color--shaft;
		font-size: 14px;
		line-height: 60px;
		letter-spacing: -0.45px;
		@include screen(small) {
			font-size: 18px;
		}
	}
	h4 {
		margin: 0;
		color: $color--shaft;
		font-size: 14px;
		line-height: 70px;
		letter-spacing: -0.15px;
	}
	h5 {
		margin: 0;
		color: $color--shaft;
		font-size: 15px;
		line-height: 18px;
		letter-spacing: 0;
	}
	h6 {
		color: $color--montana;
		font-size: 14px;
		line-height: 24px;
		@include screen(small) {
			font-size: 16px;
			line-height: 26px;
		}
	}
	p {
		color: $color--montana;
		font: 400 12px/20px $font;
		strong {
			font-weight: 700;
		}
	}
	label {
		display: block;
		margin: 0 0 15px;
		padding: 0;
		color: $color--montana;
		font: 400 15px/18px $font;
		&.wph-label--border {
			cursor: text;
			margin-bottom: 30px;
			padding-bottom: 24px;
			border-bottom: 1px solid $color--lilac;
			line-height: 20px;
			letter-spacing: 0;
			&.toggle-off {
				margin-bottom: 0;
				padding-bottom: 0;
				border-bottom: 0;
			}
		}
	}
	ul, ol {
		margin: 0;
		list-style: none;
		li {
			list-style: none;
		}
		ul, ol {
			@include screen(small) {
				margin-left: 30px;
			}
		}
	}
	a {
		color: $color--blue;
		text-decoration: none;
		&:hover {
			color: $color--blue;
		}
	}
	.wph-shortcode {
		min-width: 500px;
		display: inline-block;
		padding: 10px;
		border: 1px solid $color--gainsboro;
		border-radius: 3px;
		background: $color--concrete;
		color: $color--montana;
		font: 400 15px/18px $font;
		letter-spacing: 0px;
		&:not(:last-child) {
			margin-bottom: 15px;
		}
	}
}