.hustle-two {
	.dev-tip {
		width: 420px;
		position: relative;
		margin: 0 auto 10px;
		padding: 20px;
		border-radius: 5px;
		background-color: $color--shaft;
		&:after {
			content: " ";
			position: absolute;
			bottom: -14px;
			left: 50%;
			margin-left: -7px;
			border: 7px solid transparent;
			border-top-color: $color--shaft;
		}
		&, p {
			font-size: 15px;
			line-height: 18px;
			color: $color--dove;
		}
	}
	[tooltip] {
		overflow: visible;
		position: relative;
		&:before {
			content: " ";
			opacity: 0;
			position: absolute;
			z-index: -1;
			bottom: 100%;
			left: 50%;
			margin-bottom: -5px;
			margin-left: -5px;
			border: 5px solid transparent;
			border-top-color: $color--shaft;
			pointer-events: none;
			transition: margin .2s, opacity .2s, z-index .2s linear .2s;
		}
		&:after {
			content: attr(tooltip);
			width: 200px;
			opacity: 0;
			position: absolute;
			z-index: -1;
			bottom: 100%;
			left: 50%;
			margin-left: -100px;
			margin-bottom: 5px;
			padding: 5px;
			border-radius: 4px;
			background: $color--shaft;
			color: $color--white;
			font: 400 13px/18px $font;
			text-align: center;
			white-space: pre-wrap;
			pointer-events: none;
			transition: margin .2s, opacity .2s, z-index .2s linear .2s;
		}
		&.tooltip-s:after {
			width: 150px;
			margin-left: -75px;
		}
		&.tooltip-l:after {
			width: 280px;
			margin-left: -140px;
		}
		&.tooltip-right:after {
			margin-left: -180px;
		}
		&.tooltip-left:after {
			margin-left: -20px;
		}
		&.tooltip-s.tooltip-right:after {
			margin-left: -130px;
		}
		&.tooltip-s.tooltip-left:after {
			margin-left: -30px;
		}
		&.tooltip-l.tooltip-right:after {
			margin-left: -260px;
		}
		&.tooltip-bottom:before {
			top: 100%;
			bottom: auto;
			margin-top: -5px;
			margin-bottom: 0;
			border-color: transparent;
			border-bottom-color: $color--shaft;
		}
		&.tooltip-bottom:after {
			top: 100%;
			bottom: auto;
			margin-top: 5px;
			margin-bottom: 0;
		}
		&:hover:before {
			opacity: 1;
			z-index: 1;
			margin-bottom: 0;
			transition: margin .2s, opacity .2s;
		}
		&:hover:after {
			opacity: 1;
			z-index: 1;
			margin-bottom: 10px;
			transition: margin .2s, opacity .2s;
		}
	}
	.disabled[tooltip] {
		&:before, &:after {
			display: none;
		}
	}
}