.hustle-two .wph-toggletabs {
	border-bottom: 1px solid $color--lilac;
	&:last-child,
	&:last-of-type {
		border-bottom: 0;
	}
	.wph-toggletabs--title {
		cursor: pointer;
		position: relative;
		margin-bottom: -1px;
		padding: 1px 30px;
		h4 {
			line-height: 70px;
		}
		.open {
			position: absolute;
			top: 30px;
			right: 30px;
			.wph-icon {
				display: block;
				vertical-align: middle;
				font-size: 13px;
			}
		}
	}
	.wph-toggletabs--content,
	.wph-toggletabs--footer {
		display: none;
	}
	.wph-toggletabs--content {
		padding: 40px 0 0;
	}
	.wph-toggletabs--footer {
		margin-top: 5px;
		padding: 29px 0 0;
		border-top: 1px solid $color--lilac;
		.col-xs-12 {
			&:first-child {
				padding-left: 0;
			}
			&:last-child {
				padding-right: 0;
			}
		}
		.wph-button {
			width: auto;
			display: inline-block;
			.on-action {
				display: none;
				position: relative;
				padding-left: 28px;
				text-transform: none;
				&:before {
					content: " ";
					width: 18px;
					height: 18px;
					display: block;
					position: absolute;
					left: 0;
					border: 2px solid $color--white;
					border-bottom-color: transparent;
					border-radius: 100%;
					background: 0 0;
					box-sizing: border-box;
					animation: loading .75s 0s linear infinite;
				}
			}
			&.wph-button-save--loading,
			&.wph-button-next--loading {
				opacity: 0.7;
				.off-action {
					display: none;
				}
				.on-action {
					display: block;
				}
			}
			&.wph-button-save {
				//min-width: 184px;
			}
		}
		.next-button,
		.col-xs-12:last-child {
			.wph-button-save {
				margin-right: 46px;
			}
			.spinner-container {
				display: none;
			}
			@include screen(small) {
				text-align: right;
			}
		}
		.wph-button[disabled],
		.wph-button.disabled {
			opacity: 0.4;
		}
	}
	&.wph-toggletabs--open {
		padding: 0 30px 30px;
		.wph-toggletabs--title {
			padding: 1px 0 0;
			border-bottom: 1px solid $color--lilac;
			.open {
				top: 30px;
				right: -1px;
			}
			.i-arrow {
				transform: rotate(180deg);
			}
		}
		.wph-toggletabs--content,
		.wph-toggletabs--footer {
			display: block;
		}
	}
}