$max-tab-count: 2;

%content-edit {
	#wph-optin--content {
		border-bottom: 1px solid $color--lilac;
		> div:first-child {
			position: relative;
			@extend %left-column;
		}
		> div:last-child {
			@extend %right-column;
		}
		.row {
			padding-top: 0;
			.col-lg-9 {
				@include screen(small) {
					padding-left: 0;
				}
				@include screen(large) {
					max-width: 72.5%;
					flex-basis: 72.5%;
				}
			}
			.col-lg-3 {
				@include screen(small) {
					padding-right: 0;
				}
				@include screen(large) {
					max-width: 27.5%;
					flex-basis: 27.5%;
				}
			}
			label {
				margin: 0 0 4px;
				line-height: 19px;
				letter-spacing: 0;
			}
		}
		@include screen(large) {
			margin-bottom: 25px;
			padding-bottom: 33px;
		}
	}
	#wph-optin--messages {
		margin-top: 20px;
		.wpoi-wysiwyg-wrap {
			display: block;
			position: relative;
			.wysiwyg-tab {
				display: none;
				@for $i from 1 through $max-tab-count {
					&:checked:nth-of-type(#{$i}) ~ .wysiwyg-tab__content:nth-of-type(#{$i}) {
						visibility: visible;
						position: relative;
						z-index: 10;
						top: -1px;
						opacity: 1;
						transform: translateY(0);
						transition: 0.5s opacity ease-in, 0.8s transform ease;
					}
				}
				&:checked + label {
					opacity: 1;
					z-index: 11;
					top: 0;
					padding: 7px 14px;
				}
				+ label {
					opacity: 0.7;
					display: inline-block;
					position: relative;
					top: 2px;
					z-index: 9;
					margin: 0;
					padding: 4px 14px 7px;
					border: 1px solid $color--athens;
					border-bottom: 0;
					background: $color--smoke;
					box-sizing: border-box;
					color: $color--montana;
					font: 400 13px/18px $font;
					letter-spacing: -0.05px;
					text-align: center;
					user-select: none;
					transition: 0.3s background ease;
				}
				&__content {
					width: 100%;
					visibility: hidden;
					position: absolute;
					z-index: 1;
					left: 0;
					opacity: 0;
					background: transparent;
					transform: translateY(-3px);
					#wp-optin_message-editor-tools,
					#wp-success_message-editor-tools {
						display: none;
					}
				}
			}
			[for="wpoi-om"],
			[for="wpoi-sm"] {
				cursor: pointer;
			}
		}
	}
	#optin_image_style {
		.tabs-header {
			margin: 0;
			li {
				min-width: 1px;
				max-width: 50%;
				flex-basis: 50%;
				padding: 0 12px;
			}
			label {
				margin: 0;
				padding: 4px 0 5px;
			}
		}
	}
}