#wph-welcome-on, #wph-welcome-off {
	.box-content {
		.wph-table {
			tbody tr {
				&:first-child {
					td, th {
						padding-top: 0;
						border-top: 0;
					}
				}
				th, td {
					text-align: center;
				}
				th {
					@include screen(small) {
						text-align: left;
					}
				}
				td {
					@include screen(small) {
						text-align: right;
					}
				}
				&:last-child {
					td, th {
						padding-bottom: 0;
					}
				}
			}
		}
		.row .col-lg-4 {
			.content-322 {
				margin: 10px auto 20px;
				text-align: center;
				@include screen(medium) {
					position: relative;
					top: 50%;
					margin: 0 auto;
					transform: translateY(-50%);
				}
			}
			@include screen(medium) {
				height: 239px;
				transform-style: preserve-3d;
			}
		}
		.row .col-lg-5 {
			.content-374 {
				max-width: 100%;
				margin: 10px auto 30px 0;
				@include screen(small) {
					position: relative;
					top: 50%;
					transform: translateY(-50%);
				}
				@include screen(medium) {
					max-width: 374px;
					margin: 0 0 0 auto;
				}
			}
			@include screen(small) {
				height: 239px;
				transform-style: preserve-3d;
			}
		}
	}
}