%css-edit {
	#wph-optin--css {
		> div:first-child {
			position: relative;
			@extend %left-column;
		}
		> div:last-child {
			@extend %right-column;
			.tabs .tabs-header {
				margin: 0;
				li {
					min-width: 130px;
				}
			}
		}
		.wph-label--border {
			.toggle {
				margin-top: -3px;
				margin-right: 7px;
			}
		}
		@include screen(small) {
			padding-bottom: 25px;
		}
	}
	#wph-css-holder {
		label {
			margin-bottom: 14px;
			letter-spacing: 0px;
		}
		.wpoi-css-selectors {
			margin-bottom: 6px;
		}
	}
}