#wph-welcome {
	.box-content {
		.row .col-lg-9 {
			.content-800 {
				margin: 10px auto 30px;
				text-align: center;
				@include screen(small) {
					margin: 0 0 0 7px;
					text-align: left;
				}
				@include screen(small) {
					position: relative;
					top: 50%;
					transform: translateY(-50%);
				}
			}
			@include screen(small) {
				height: 239px;
				transform-style: preserve-3d;
			}
		}
	}
}