%left-column {
	&.col-xs-12 {
		padding-left: 0;
	}
	&.col-lg-3 {
		@include screen(large) {
			max-width: 21%;
			flex-basis: 21%;
		}
	}
}

%right-column {
	&.col-xs-12 {
		padding-right: 0;
		padding-left: 17px;
	}
	&.col-lg-9 {
		@include screen(large) {
			max-width: 79%;
			flex-basis: 79%;
		}
	}
}

#cc-wizard {
	.box .box-title {
		h3 {
			line-height: 59px;
		}
	}
	.box .box-content {
		padding: 0;
		#wph-ccontent--name,
		#wph-ccontent--content,
		#wph-ccontent--optional,
		#wph-ccontent--design {
			> div:first-child {
				position: relative;
				@extend %left-column;
			}
			> div:last-child {
				@extend %right-column;
			}
		}
		#wph-ccontent--name {
			padding-bottom: 24px;
		}
		#wph-ccontent--content {
			.wph-label--border {
				margin-bottom: 40px;
			}
			.row:not(:last-child) {
				padding-top: 0;
			}
			.row .col-xs-12 {
				&:first-child {
					padding-left: 0;
				}
				&:last-child {
					padding-right: 0;
				}
			}
			#wph-ccontent--msg {
				margin-bottom: 24px;
				.col-lg-9 {
					@include screen(small) {
						padding-left: 0;
					}
					@include screen(large) {
						max-width: 72.5%;
						flex-basis: 72.5%;
					}
				}
				.col-lg-3 {
					@include screen(small) {
						padding-right: 0;
					}
					@include screen(large) {
						max-width: 27.5%;
						flex-basis: 27.5%;
					}
				}
				.wph-media--holder {
					margin-bottom: 10px;
				}
			}
			.tabs .tabs-header {
				margin: 0 0 17px;
				li {
					width: 50%;
					min-width: 1px;
					label {
						margin: 0;
						padding: 7px 5px 6px;
						font-size: 15px;
					}
				}
			}
			.wph-label--checkbox {
				> label {
					padding-left: 28px;
					letter-spacing: 0px;
				}
				label + .wph-input--checkbox {
					top: 3px;
				}
			}
		}
		#wph-ccontent--optional {
			padding-bottom: 24px;
			.row {
				padding: 0;
				.col-sm-4 {
					label {
						margin-bottom: 5px;
						letter-spacing: 0px;
					}
					.select2-container {
						width: 100% !important;
						max-width: 200px;
					}
					&:nth-child(1) {
						@include screen(small) {
							max-width: 31.7%;
							flex-basis: 31.7%;
							padding-left: 0;
						}
					}
					&:nth-child(2) {
						@include screen(small) {
							max-width: 32.4%;
							flex-basis: 32.4%;
							padding-left: 5px;
						}
					}
					&:nth-child(3) {
						@include screen(small) {
							max-width: 35.9%;
							flex-basis: 35.9%;
							padding-left: 5px;
							padding-right: 0;
						}
					}
					@include screen(small) {
						padding: 0 25px;
					}
				}
			}
		}
		#wph-ccontent--design {
			margin-bottom: 15px;
			padding-bottom: 40px;
			border-bottom: 1px solid $color--lilac;
			> .col-xs-12:last-child {
				> .wph-label--border {
					margin-bottom: 0;
				}
				h4 + label {
					cursor: text;
					display: inline-block;
					margin-top: -8px;
					margin-bottom: 0;
					letter-spacing: 0px;
				}
			}
		}
		#wph-ccontent--border,
		#wph-ccontent--shadow,
		#wph-ccontent--customsize {
			margin-top: 25px;
			.wph-label--checkbox {
				> label {
					display: inline-block;
					padding-left: 28px;
					letter-spacing: 0px;
				}
				label + .wph-input--checkbox {
					top: 4px;
				}
			}
		}
		#wph-ccontent--shadow {
			margin-top: 26px;
		}
		#wph-ccontent--customsize {
			margin-top: 26px;
		}
		#wph-ccontent--css {
			> div:first-child {
				position: relative;
				@extend %left-column;
			}
			> div:last-child {
				@extend %right-column;
				.tabs .tabs-header {
					margin: 0;
					li {
						min-width: 130px;
					}
				}
			}
			.wph-label--border {
				.toggle {
					margin-top: -3px;
					margin-right: 7px;
				}
			}
			@include screen(small) {
				padding-bottom: 25px;
			}
		}
	}
	#wph-ccontent--palette {
		.select2-container {
			width: 100% !important;
			max-width: 341px;
			margin-right: 27px;
			.wpmui-select2.select2-selection--single .select2-selection__rendered,
			.wpmui-select2.select2-selection--multiple .select2-selection__rendered {
				color: $color--wedgewood;
			}
			+ .wph-label--checkbox {
				display: inline-block;
				position: relative;
				top: 1px;
				> label {
					padding-top: 0;
					padding-left: 29px;
					letter-spacing: 0px;
				}
			}
		}
	}
	#wph-ccontent--custom_colors,
	#wph-ccontent--border_settings,
	#wph-ccontent--shadow_settings,
	#wph-ccontent--customsize_settings {
		position: relative;
		margin-top: 20px;
		padding: 1px 15px 30px;
		border-radius: 5px;
		background: $color--storm;
		h4 {
			padding: 0 15px;
			line-height: 68px;
		}
		.wph-label--alt {
			cursor: text;
			margin: 0 0 5px;
			letter-spacing: 0px;
		}
		.wph-input--number {
			width: 70px;
			input {
				padding-left: 14px;
			}
		}
		.select2-container {
			width: 150px !important;
		}
		.row {
			position: relative;
			padding-top: 0;
		}
		.ocp-basic-other {
			padding-bottom: 1px;
		}
		.ocp-basic-content {
			z-index: 8;
		}
		.ocp-basic-other {
			z-index: 7;
		}
	}
	#wph-ccontent--border_settings,
	#wph-ccontent--shadow_settings,
	#wph-ccontent--customsize_settings {
		margin-top: 14px;
		padding-top: 20px;
		.wph-pickers--single {
			margin-top: 10px;
		}
	}
	#wph-ccontent--custom_colors {
		z-index: 5;
	}
	#wph-ccontent--border_settings {
		z-index: 4;
	}
	#wph-ccontent--shadow_settings {
		z-index: 3;
	}
	#wph-ccontent--customsize_settings {
		padding-bottom: 32px;
		.wph-input--number {
			width: 100px;
		}
	}
	#wph-css-holder {
		label {
			margin-bottom: 14px;
			letter-spacing: 0px;
		}
		.wpoi-css-selectors {
			margin-bottom: 6px;
		}
	}
	// SETTINGS
	#wph-ccontent--information,
	#wph-ccontent--popup,
	#wph-ccontent--slidein {
		> div:first-child {
			position: relative;
			@extend %left-column;
		}
		> div:last-child {
			@extend %right-column;
		}
	}
	#wph-ccontent--popup_conditions,
	#wph-ccontent--popup_triggers,
	#wph-optin--slidein_conditions {
		p {
			margin: 0 0 31px;
			letter-spacing: 0px;
		}
	}
	#wph-ccontent--popup {
		padding-bottom: 25px;
	}
	#wph-ccontent--slidein {
		padding-bottom: 35px;
	}
	#wpoi-wizard-settings-widget-message {
		.row {
			margin-bottom: 10px;
			padding: 0 0 30px;
			border-bottom: 1px solid $color--lilac;
		}
		label {
			line-height: 20px;
			letter-spacing: 0px;
			&:nth-child(1) {
				margin-bottom: 20px;
			}
			&:nth-child(2) {
				margin-bottom: 14px;
			}
		}
	}
	.wph-trigger {
		#wpoi-triggers-popup-scroll {
			input[type="text"] {
				max-width: 380px;
				margin-left: 28px;
			}
		}
	}
	#wph-ccontent--popup_triggers,
	.wph-ccontent-slidein_triggers,
	#wph-ccontent--slidein_options,
	#wph-ccontent--slidein_expiricy {
		padding-bottom: 28px;
		border-bottom: 1px solid $color--lilac;
	}
	#wph-ccontent--popup_animation {
		padding-bottom: 30px;
		border-bottom: 1px solid $color--lilac;
		.row {
			padding: 0;
			.col-xs-12:first-child {
				@include screen(small) {
					padding-left: 0;
				}
			}
			.col-xs-12:last-child {
				@include screen(small) {
					padding-right: 0;
				}
			}
			label {
				cursor: text;
				margin-bottom: 9px;
			}
			.select2-container {
				width: 100% !important;
				max-width: 310px;
			}
		}
	}
	.wph-ccontent--popup_settings {
		.wph-ccontent--additional {
			margin-bottom: 20px;
		}
		.wph-ccontent--expiricy {
			padding-bottom: 30px;
			border-bottom: 1px solid $color--lilac;
			label {
				margin-bottom: 5px;
			}
			.wph-label--number {
				> label {
					left: 75px;
				}
				.wph-input--number {
					width: 70px;
				}
			}
		}
		.wph-ccontent--formsubmit {
			.select2-container {
				width: 100% !important;
				max-width: 300px;
			}
		}
	}
}