.wph-update-wrapper {
	opacity: 1;
	visibility: visible;
	position: fixed;
	z-index: 99;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	margin-top: 32px;
	transition: none;
	transform-style: preserve-3d;
	transition: opacity 0.2s ease;
	@include screen(small) {
		left: 160px;
	}
	.wph-update-mask {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background: $color--modal;
	}
	.wph-update-body {
		width: 100%;
		max-width: 680px;
		opacity: 1;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		transition: opacity 0.3s ease, transform 0.25s linear;
		.box {
			margin: 0;
		}
	}
	.box .box-title {
		height: auto;
		position: relative;
		margin: 0;
		h3 {
			padding: 7px 0 3px;
			line-height: 51px;
			span.wpoi-total-subscribers {
				display: none;
				margin-left: 15px;
				color: $color--montana;
				font-size: 16px;
				font-family: $font;
				font-weight: 400;
				text-transform: none;
				@include screen(small) {
					display: inline;
				}
			}
		}
		.wph-icon {
			top: 23px;
			right: 30px;
			font-size: 13px;
		}
	}
	.box .box-content {
		padding-top: 30px;
		padding-bottom: 30px;
		.col-sm-4, .col-md-4, .col-lg-4 {
			@include screen(small) {
				max-width: 25.7%;
				flex-basis: 25.7%;
				padding-right: 0;
			}
		}
		.col-sm-8, .col-md-8, .col-lg-8 {
			@include screen(small) {
				max-width: 74.3%;
				flex-basis: 74.3%;
			}
		}
		.wph-img--soon {
			margin: 0;
		}
		.wph-update-msg {
			position: relative;
			h3 {
				margin-top: 30px;
				color: $color--montana;
				font-family: $font;
				line-height: 24px;
				letter-spacing: -1px;
				text-transform: none;
			}
			h6 {
				margin-top: 1px;
				letter-spacing: 0.1px;
			}
		}
		.wph-update-details {
			padding-top: 14px;
			ul {
				max-width: 530px;
				margin: 0 auto;
				padding-left: 7px;
			}
			ul li {
				margin-bottom: -1px;
				color: $color--montana;
				font: 500 14px/26px $font;
				&:before {
					content: $i-check;
					position: relative;
					top: -1px;
					margin-right: 9px;
					color: $color--blue;
					font-size: 10px;
					font-family: $font--icon;
				}
				a {
					padding-bottom: 2px;
					border-bottom: 1px solid rgba(23, 168, 227, 0.2);
					&:hover {
						border-bottom-color: $color--blue;
					}
				}
			}
		}
		.row:last-child {
			text-align: center;
		}
	}
	&.hide {
		opacity: 0;
		visibility: hidden;
		.wph-update-body {
			opacity: 0;
			transform: translate(-50%, 20%);
		}
	}
}

.wp-admin.folded .wph-update-wrapper {
	@include screen(small) {
		left: 36px;
	}
}