%basic-setup {
	#wph-optin--name {
		> div:first-child {
			@extend %left-column;
		}
		> div:last-child {
			@extend %right-column;
		}
	}
	#wph-optin--mode {
		padding-top: 24px;
		> div:first-child {
			@extend %left-column;
		}
		> div:last-child {
			@extend %right-column;
			label + .row {
				padding: 0 0 5px;
				border-bottom: 1px solid $color--lilac;
			}
			label:not(.toggle-label):not(.wph-label--border) {
				color: $color--shaft;
				font-size: 14px;
				line-height: 20px;
				letter-spacing: 0;
				small {
					display: block;
					color: $color--monsoon;
					font-size: 12px;
					letter-spacing: -0.05px;
				}
				+ .toggle {
					position: absolute;
					top: 0;
				}
			}
			#wph-optin--testmode {
				position: relative;
				padding-left: 0;
				label:not(.toggle-label) {
					padding-left: 50px;
					+ .toggle {
						left: 0;
					}
				}
			}
			#wph-optin--localemails {
				position: relative;
				padding-right: 70px;
				label:not(.toggle-label) {
					padding-left: 64px;
					+ .toggle {
						left: 29px;
					}
				}
			}
		}
	}
	#wph-optin--service {
		padding-top: 9px;
		> div:first-child {
			@extend %left-column;
		}
		> div:last-child {
			@extend %right-column;
			h4 {
				line-height: 22px;
			}
			.tabs .tabs-header {
				margin-top: 24px;
				li {
					min-width: 130px;
					padding: 0;
					label {
						padding: 8px 0 6px;
						font: 500 12px/14px $font;
						letter-spacing: -0.05px;
					}
				}
			}
			.tabs .tabs-body {
				margin-top: 24px; // Remove if .tab-header is enabled
				.wpoi-error-select {
					margin-bottom: 10px;
				}
			}
			.tabs .tabs-content {
				.select2-container {
					width: 100% !important;
				}
				.wph-table tbody {
					tr:first-child td {
						padding-top: 0;
						border-top: 0;
					}
					tr td {
						padding: 20px 0 9px;
					}
					.wph-list--radio {
						width: 36px;
					}
					.wph-list--icon {
						width: 44px;
					}
					.wph-list--info {
						.wph-table--title {
							letter-spacing: -0.05px;
						}
						.wph-table--subtitle {
							letter-spacing: 0px;
						}
					}
					.wph-list {
						&--mailchimp,
						&--campaignmonitor {
							width: 38px;
							height: 43px;
							display: block;
							margin: 0 auto;
							@extend %sprite;
						}
						&--mailchimp {
							background-position: 1px -186px;
						}
						&--campaignmonitor {
							background-position: -57px -186px;
						}
					}
				}
			}
		}
	}
}