%image-location {
	#optin_image_location {
		margin: 0 0 29px;
		.tabs .tabs-header {
			margin: 0;
			li {
				padding: 0 10px;
				label {
					padding: 8px 0 6px;
					letter-spacing: 0.05px;
				}
			}
			li:nth-child(1),
			li:nth-child(2) {
				min-width: 100px;
			}
			li:nth-child(3),
			li:nth-child(4) {
				min-width: 98px;
				padding: 0;
			}
		}
	}
}