@import "admin/templates/preview/_tpl-open.scss";
@import "admin/templates/preview/_tpl-mask.scss";
@import "admin/templates/preview/_tpl-close.scss";

.hustle-two {
	@extend %preview-open;
}

.hustle-two .wph-preview {
	position: fixed;
	z-index: 999999;
	top: 32px;
	right: 0;
	bottom: 0;
	left: 160px;
	transform-style: preserve-3d;
	&.wph-preview--open {
		opacity: 1;
		visibility: visible;
		display: block;
		transition: opacity 0.3s ease-in;
	}
	&.wph-preview--closed {
		opacity: 0;
		visibility: hidden;
		display: none;
	}
	.wph-preview--optin {
		width: 100%;
		max-width: 720px;
		position: absolute;
		z-index: 99;
		&.wph-preview--popup {
			top: 50%;
			left: 50%;
			transform: translate(-50%,-50%);
		}
		&.wph-preview--slide_in {
			position: fixed;
			right: 0;
			bottom: 0;
		}
		&.wph-preview--after_content {
			max-width: 100%;
			right: 0;
			bottom: 0;
			left: 0;
			padding: 30px;
			background: $color--white;
			.wph-preview--holder {
				max-width: 720px;
				margin: 0 0 0 auto;
			}
		}
	}
	.wph-modal.wph-modal--popup,
	.wph-modal.wph-modal--slide_in,
	.wph-modal.wph-modal--magic_bar {
		position: absolute;
	}
	.wph-modal.wph-modal--popup {
		@media (max-width: 782px) {
			margin-top: 23px;
		}
	}
	.wph-modal.wph-modal--magic_bar {
		top: 32px;
	}
	@extend %preview-mask;
	@extend %preview-close;
	@media (max-width: 960px) {
		left: 36px;
	}
	@media (max-width: 782px) {
		top: 0;
		left: 0;
	}
}

body.folded .hustle-two .wph-preview {
	left: 36px;
	@media (max-width: 782px) {
		left: 0;
	}
}