.hustle-two {
	input[type="text"], textarea {
		width: 100%;
		display: block;
		margin: 0;
		padding: 11px 9px 9px;
		border: 1px solid $color--gainsboro;
		border-radius: 3px;
		outline: none;
		background: $color--white;
		box-shadow: none;
		color: $color--montana;
		font-weight: 400;
		font-size: 15px;
		&:last-child {
			margin-bottom: 0;
		}
		&:-moz-placeholder {
			color: $color--sand;
		}
		&::-moz-placeholder {
			color: $color--sand;
		}
		&:-ms-input-placeholder {
			color: $color--sand;
		}
		&::-webkit-input-placeholder {
			color: $color--sand;
		}
	}
	textarea {
		height: 210px;
		resize: none;
	}
	select.wpmuiSelect {
		display: none;
	}
	.select2-container {
		width: auto !important;
		&, * {
			outline: none;
		}
		.wpmui-select2 {
			&.select2-selection--single,
			&.select2-selection--multiple {
				height: auto;
				min-height: auto;
				border: 1px solid $color--gainsboro;
				border-radius: 3px;
				background: $color--white;
				color: $color--montana;
				font: 400 15px/18px $font;
				.select2-selection__rendered {
					padding: 10px 45px 11px 15px;
					color: $color--montana;
					font: 400 15px/18px $font;
				}
				.select2-selection__arrow {
					width: 15px;
					height: 14px;
					top: 12px;
					right: 15px;
					&:before {
						content: $i-burger;
						color: $color--iron;
						font-size: 15px;
						line-height: 15px;
						font-family: $font--icon;
					}
					b {
						display: none !important;
					}
				}
			}
			&.select2-selection--multiple {
				.select2-selection__rendered {
					padding: 0 3px 8px;
				}
				.select2-selection__rendered li {
					margin-top: 3px;
					margin-bottom: 0;
					font-size: 12px;
					line-height: 22px;
				}
				.select2-selection__rendered li.select2-selection__choice {
					position: relative;
					padding: 1px 27px 0 4px;
					border: 0;
					background: $color--manatee;
					color: $color--white;
					letter-spacing: 0.5px;
					.select2-selection__choice__remove {
						width: 9px;
						height: 9px;
						overflow: hidden;
						display: block;
						position: absolute;
						top: 7px;
						right: 5px;
						margin: 0;
						color: $color--manatee;
						&:before {
							width: 9px;
							height: 9px;
							content: $i-remove;
							opacity: 0.7;
							display: block;
							color: $color--gallery;
							font-size: 8px;
							line-height: 9px;
							font-family: $font--icon;
							transition: opacity 0.2s ease;
						}
						&:hover:before {
							opacity: 1;
						}
					}
				}
			}
		}
	}
	.wph-input--radio,
	.wph-input--checkbox {
		width: 18px;
		height: 18px;
		display: inline-block;
		margin: 0 10px 0 0;
		input {
			display: none;
			& + label {
				width: 18px;
				height: 18px;
				cursor: pointer;
				display: block;
				margin: 0;
				border-radius: 3px;
				background: $color--pale;
				&:before {
					display: block;
					padding: 1px 0 0;
					border-radius: 3px;
					color: $color--pale;
					font-size: 10px;
					line-height: 18px;
					text-align: center;
				}
			}
			&:checked + label {
				background: $color--blue;
				&:before {
					color: $color--white;
				}
			}
		}
	}
	.wph-input--radio {
		input {
			& + label {
				border-radius: 100%;
				&:before {
					border-radius: 100%;
				}
			}
		}
	}
	.wph-input--number {
		width: 80px;
		position: relative;
		input {
			width: 100%;
			height: 40px;
			display: block;
			margin: 0;
			padding: 0 20px 0 4px;
			border: 1px solid $color--gainsboro;
			border-radius: 3px;
			color: $color--montana;
			font: 400 15px/18px $font;
			-webkit-appearance: textfield;
			-moz-appearance: textfield;
			&:focus {
				outline: 0;
			}
			&:hover, &:focus, &:active {
				+ .wph-nbr--nav {
					opacity: 1;
					visibility: visible;
				}
			}
		}
		input::-webkit-inner-spin-button,
		input::-webkit-outer-spin-button {
			display: none;
			margin: 0;
			-webkit-appearance: none;
		}
		.wph-nbr--nav {
			width: 16px;
			height: 40px;
			opacity: 0;
			visibility: hidden;
			position: absolute;
			top: 0;
			right: 0;
			border: 1px solid $color--gainsboro;
			border-radius: 0 3px 3px 0;
			background: $color--flash;
			transition: 0.2s ease-in;
		}
		.wph-nbr--button {
			width: 100%;
			cursor: pointer;
			user-select: none;
			position: relative;
			color: $color--montana;
			font-size: 13px;
			line-height: 1.5;
			font-family: "Trebuchet MS", Helvetica, sans-serif;
			text-align: center;
			&.wph-nbr--up {
				height: 50%;
				position: absolute;
				top: 0;
			}
			&.wph-nbr--down {
				height: 50%;
				position: absolute;
				bottom: 0;
			}
		}
		&:hover {
			.wph-nbr--nav {
				opacity: 1;
				visibility: visible;
			}
		}
	}
}

.wp-admin .select2-container .select2-dropdown {
	&.wpmui-select2 {
		margin-top: -3px;
		border: 1px solid $color--gainsboro;
		border-top: 0;
		border-radius: 0 0 3px 3px;
		box-shadow: none;
		.select2-search {
			display: none;
		}
		.select2-results {
			&, .select2-results__option[aria-selected=true] {
				color: $color--montana;
				font: 300 14px/18px $font;
			}
			.select2-results__option {
				padding: 6px 15px;
				&:before {
					display: none;
				}
			}
			.select2-results__option--highlighted,
			.select2-results__option--highlighted[aria-selected=true] {
				background: $color--gainsboro;
				color: $color--montana;
				font: 300 14px/18px $font;
			}
		}
	}
}

.wp-admin .select2-container--default .select2-results__option--highlighted[aria-selected] {
	background: $color--gainsboro;
	color: $color--montana;
	font: 300 14px/18px $font;
}