// Making sure dash has a unified background color
body {
	background: $color--wild !important;
}

// Making sure dash has the correct left padding to match our grid
#wpcontent {
	padding-left: 15px;
}

// Making sure dash notice button keep the default box-sizing
#wpmu-install-dashboard .wpmu-button {
	box-sizing: content-box;
}

// Making sure dash notice matches Hustle grid
.wdpun-notice.notice {
	margin-top: 30px;
	margin-right: 30px;
	margin-left: 15px;
}

.hustle-two {
	padding-right: 10px;
	* {
		box-sizing: border-box;
		text-rendering: geometricPrecision;
		-moz-osx-font-smoothing: grayscale;
		-webkit-font-smoothing: antialiased;
	}
	#container {
		width: 100%;
		max-width: 1176px;
		margin: 30px 0 0;
		color: $color--montana;
		font: 400 15px/1.5em $font;
		letter-spacing: -0.015em;
		&.container-1146 {
			max-width: 1176px;
		}
		&.container-980 {
			max-width: 1010px;
		}
		#header {
			position: relative;
			margin: 0 0 30px;
			padding: 0 15px;
			h1 {
				margin: 0;
			}
			h1 .wph-button {
				display: none;
				position: relative;
				margin-left: 23px;
				padding: 0 15px;
				letter-spacing: 0;
				@include screen(small) {
					display: inline-block;
					top: -6px;
				}
			}
		}
	}
	.hidden {
		display: none !important;
	}
	.tablet-hidden {
		@media (max-width: 780px) {
			display: none !important;
		}
	}
	.mobile-hidden {
		@media (max-width: 620px) {
			display: none !important;
		}
	}
	.disabled, .disabled * {
		opacity: 0.7;
		pointer-events: none;
	}
	.dashicons-warning,
	.dashicons-editor-help {
		cursor: pointer;
		color: $color--cardinal;
	}
	.wph-tag {
		&--soon,
		&--premium {
			display: inline-block;
			position: relative;
			top: -2px;
			margin-left: 10px;
			padding: 1px 15px 0;
			border-radius: 24px;
			color: $color--shaft;
			font: 400 12px/22px $font;
			letter-spacing: -0.3px;
			text-transform: uppercase;
			text-align: center;
		}
		&--soon {
			background: $color--corn;
		}
		&--premium {
			background: $color--patblue;
		}
	}
	.wph-label--notice,
	.wph-label--loading {
		height: auto;
		opacity: 1;
		cursor: default;
		visibility: visible;
		position: relative;
		margin: 0 0 15px;
		padding: 14px 15px 12px;
		border-radius: 3px;
		background: $color--bleach;
		color: $color--notice;
		font: 400 12px/14px $font;
		letter-spacing: 0;
		transition: height 0.3s linear, opacity 0.3s ease;
		&:before {
			content: " ";
			width: 18px;
			height: 18px;
			display: block;
			position: absolute;
			top: 11px;
			left: 15px;
			@extend %sprite;
			background-position: 0 -120px;
		}
		span {
			opacity: 0.9;
			padding-left: 28px;
		}
		&.disabled {
			height: 0;
			opacity: 0;
			visibility: hidden;
			margin: 0;
			padding: 0;
		}
	}
	.wph-label--loading {
		background: $color--sky;
		&:before {
			border: 2px solid $color--white;
			border-bottom-color: transparent;
			border-radius: 100%;
			background: 0 0;
			box-sizing: border-box;
			animation: loading .75s 0s linear infinite;
		}
	}
	.wph-label--checkbox,
	.wph-label--radio {
		position: relative;
		label {
			margin: 0;
			cursor: pointer;
		}
		> label {
			display: block;
			padding: 2px 0 0 35px;
			line-height: 20px;
		}
		label + .wph-input--checkbox,
		label + .wph-input--radio {
			position: absolute;
			top: 0;
			left: 0;
			margin: 0;
		}
		label + .wph-input--checkbox,
		label + .wph-input--radio {
			top: 1px;
		}
	}
	.wph-label--number {
		display: block;
		position: relative;
		> label {
			cursor: text;
			position: absolute;
			top: 11px;
			left: 85px;
		}
		label + .wph-input--number {
			display: block;
		}
	}
	.wph-label--toggle {
		position: relative;
		margin-bottom: 15px;
		> label {
			display: inline-block;
			margin: 0;
			padding-left: 43px;
			line-height: 22px;
		}
		label + .toggle {
			position: absolute;
			top: -1px;
			left: 0;
		}
	}
	.wph-label--mix {
		height: 40px;
		position: relative;
		&:not(:first-child) {
			margin-top: 9px;
		}
		&:not(:last-child) {
			margin-bottom: 7px;
		}
		> label {
			display: inline-block;
			position: relative;
			top: -1px;
			margin: 0;
			padding-left: 28px;
			letter-spacing: 0px;
			+ .wph-input--radio,
			+ .wph-input--checkbox {
				position: absolute;
				top: 10px;
				left: 0;
				+ .wph-input--number {
					width: 70px;
					display: inline-block;
					margin-left: 9px;
				}
			}
		}
		.wph-input--number {
			+ label {
				padding-left: 0;
			}
			+ .wpmuiSelect + .select2-container {
				width: 150px !important;
				top: -1px;
				margin-left: 7px;
			}
		}
	}
	.wph-sticky--anchor {
		height: 0;
	}
	@include screen(small) {
		padding-right: 15px;
	}
}

// Making sure all "welcome" and "no content" widgets has same styles
.hustle-two #wph-woop {
	.box-content {
		.row .col-lg-9 {
			.content-800 {
				margin: 10px auto 30px;
				text-align: center;
				@include screen(small) {
					margin: 0 0 0 7px;
					text-align: left;
				}
				@include screen(small) {
					position: relative;
					top: 50%;
					transform: translateY(-50%);
				}
			}
			@include screen(small) {
				height: 239px;
				transform-style: preserve-3d;
			}
		}
	}
}