.hustle-two .row {
	display: flex;
	flex-wrap: wrap;
	flex-direction: row;
	padding: 10px 0;
	&:first-child {
		padding-top: 0;
	}
	&:last-child {
		padding-bottom: 0;
	}
	@include screen(medium) {
		padding: 15px 0;
	}
	// Additional Column
	.col-eq {
		padding: 0 15px;
	}
	// Extra Small Screens
	// Nor minimum, nor maximum screen size
	.col-xs-1, .col-xs-2, .col-xs-3, .col-xs-4, .col-xs-5, .col-xs-6,
	.col-xs-7, .col-xs-8, .col-xs-9, .col-xs-10, .col-xs-11, .col-xs-12 {
		flex: 0 0 auto;
		-webkit-box-flex: 0;
		padding: 0 15px;
	}
	.col-xs-1 {
		max-width: 8.33333333%;
		flex-basis: 8.33333333%;
	}
	.col-xs-2 {
		max-width: 16.66666667%;
		flex-basis: 16.66666667%;
	}
	.col-xs-3 {
		max-width: 25%;
		flex-basis: 25%;
	}
	.col-xs-4 {
		max-width: 33.33333333%;
		flex-basis: 33.33333333%;
	}
	.col-xs-5 {
		max-width: 41.66666667%;
		flex-basis: 41.66666667%;
	}
	.col-xs-6 {
		max-width: 50%;
		flex-basis: 50%;
	}
	.col-xs-7 {
		max-width: 58.33333333%;
		flex-basis: 58.33333333%;
	}
	.col-xs-8 {
		max-width: 66.66666667%;
		flex-basis: 66.66666667%;
	}
	.col-xs-9 {
		max-width: 75%;
		flex-basis: 75%;
	}
	.col-xs-10 {
		max-width: 83.33333333%;
		flex-basis: 83.33333333%;
	}
	.col-xs-11 {
		max-width: 91.66666667%;
		flex-basis: 91.66666667%;
	}
	.col-xs-12 {
		max-width: 100%;
		flex-basis: 100%;
		&:first-child {
			@media (max-width: 767px) {
				margin-top: 0;
			}
		}
		&:last-child {
			@media (max-width: 767px) {
				margin-bottom: 0;
			}
		}
		@media (max-width: 767px) {
			margin: 10px 0;
		}
	}
	.col-xs-hidden {
		@media (max-width: 767px) {
			display: none;
		}
	}
