.hustle-two .wph-table {
	width: 100%;
	display: block;
	// Table Header
	thead {
		display: none;
		tr th {
			padding: 0;
			color: $color--shaft;
			font: 700 14px/40px $font--alt;
			letter-spacing: -0.15px;
			text-transform: uppercase;
			text-align: left;
		}
		@include screen(small) {
			display: table-header-group;
		}
	}
	// Table Body
	tbody {
		width: 100%;
		display: block;
		tr {
			width: 100%;
			display: block;
			@include screen(small) {
				display: table-row;
			}
		}
		tr th, tr td {
			width: 100%;
			display: block;
			position: relative;
			@include screen(small) {
				width: auto;
				display: table-cell;
				padding: 15px 0;
				border-top: 1px solid $color--lilac;
			}
		}
		tr th {
			color: $color--shaft;
			font: 700 15px/28px $font--alt;
			letter-spacing: -0.16px;
			text-transform: uppercase;
			text-align: left;
		}
		tr td {
			.wph-table--title,
			.wph-table--subtitle {
				overflow: hidden;
				display: block;
				color: $color--montana;
				text-overflow: ellipsis;
				white-space: normal;
			}
			.wph-table--title {
				font: 400 16px/26px $font;
			}
			.wph-table--subtitle {
				font: 400 12px/16px $font;
			}
			label:not(.wph-icon):not(.toggle-label) {
				display: block;
				margin: 0;
				padding: 0 0 0 30px;
				color: $color--montana;
				font: 400 15px/20px $font;
				+ input[type="checkbox"] {
					position: absolute;
					top: 0;
					left: 0;
					margin: 0;
					@include screen(small) {
						top: 15px;
					}
				}
				@include screen(small) {
					padding: 2px 0 0 30px;
					font: 400 15px/18px $font;
				}
			}
			.wph-button {
				width: 100%;
				display: block;
				margin: 0 auto;
				text-align: center;
				@include screen(small) {
					width: auto;
					min-width: 55px;
					margin: 0 0 0 auto;
				}
			}
			.toggle {
				display: inline-block;
				margin: 0 auto;
				@include screen(small) {
					width: 33px;
					display: block;
				}
			}
		}
		@include screen(small) {
			display: table-row-group;
		}
	}
	// Table Footer
	tfoot {
		width: 100%;
		display: block;
		tr {
			width: 100%;
			display: block;
			td {
				width: 100%;
				display: block;
				padding: 15px 0 0;
				@include screen(small) {
					width: auto;
					display: table-cell;
					padding: 46px 0 0;
					border-top: 1px solid $color--lilac;
				}
			}
			@include screen(small) {
				display: table-row;
			}
		}
		@include screen(small) {
			display: table-footer-group;
		}
	}
	@include screen(small) {
		display: table;
		border-collapse: collapse;
	}
}

.hustle-two .wph-table--fixed {
	table-layout: fixed;
	tr th, tr td {
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
}