%preview-close {
	.wph-preview--switch {
		position: fixed;
		z-index: 999; 
		top: 72px;
		right: 50px;
		.wph-flex {
			.wph-flex--side_50,
			.wph-flex--box {
				@media (max-width: 620px) {
					margin-bottom: 0;
				}
			}
			.wph-flex--side_50 + .wph-flex--box {
				@media (max-width: 782px) {
					text-align: right;
				}
			}
			@media (max-width: 620px) {
				flex-direction: row;
			}
		}
		p {
			color: $color--white;
			font: 400 15px/18px $font;
			@media (max-width: 782px) {
				display: none;
			}
		}
		.select2-container {
			width: 200px !important;
			margin-bottom: 5px;
			.wpmui-select2.select2-selection--single {
				@media (max-width: 782px){
					height: 46px;
					border: 0;
					border-radius: 0;
				}
				.select2-selection__rendered {
					@media (max-width: 782px) {
						padding: 14px 50px 14px 15px;
						border: 0;
						background: $color--cinder;
						color: $color--chateau;
					}
				}
				.select2-selection__arrow {
					&:before {
						@media (max-width: 782px) {
							color: $color--chateau;
						}
					}
					@media (max-width: 782px) {
						top: 15px;
					}
				}
			}
			@media (max-width: 782px) {
				width: auto !important;
				margin-bottom: 0;
			}
		}
		@media (max-width: 960px) {
			top: 30px;
			right: 30px;
		}
		@media (max-width: 782px) {
			width: 100%;
			top: 0;
			left: 0;
			background: $color--cinder;
		}
	}
	.wph-preview--close {
		width: 50px;
		height: 50px;
		display: block;
		margin: 0;
		padding: 0;
		border-radius: 100%;
		background: $color--white;
		box-shadow: 0 3px 5px rgba(0,0,0,0.1);
		text-align: center;
		.wph-icon {
			position: relative;
			top: 1px;
			color: $color--montana;
			@media (max-width: 782px) {
				color: $color--chateau;
			}
		}
		@media (max-width: 782px) {
			width: 46px;
			height: 46px;
			margin: 0;
			border: 0;
			border-radius: 0;
			background: $color--cinder;
		}
	}
}