%modal-emails {
	opacity: 0;
	visibility: hidden;
	position: fixed;
	z-index: 99;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	margin-top: 32px;
	transition: none;
	transform-style: preserve-3d;
	transition: opacity 0.2s ease;
	.wpoi-complete-mask {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background: $color--modal;
	}
	.wpoi-complete-wrap {
		width: 100%;
		max-width: 588px;
		opacity: 0;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, 20%);
		transition: opacity 0.3s ease, transform 0.25s linear;
		.box {
			margin: 0;
		}
	}
	.box .box-title {
		height: auto;
		position: relative;
		margin: 0;
		h3 {
			padding: 7px 0 3px;
			line-height: 51px;
			span.wpoi-total-subscribers {
				display: none;
				margin-left: 15px;
				color: $color--montana;
				font-size: 16px;
				font-family: $font;
				font-weight: 400;
				text-transform: none;
				@include screen(small) {
					display: inline;
				}
			}
		}
		.wph-icon {
			top: 23px;
			right: 30px;
			font-size: 13px;
		}
	}
	.box .box-content {
		padding: 0;
	}
	.box-content .inc-opt-close-emails-list {
		width: 22px;
		height: 22px;
		position: absolute;
		top: 25px;
		right: 30px;
		color: $color--montana;
		&:hover {
			color: $color--shaft;
		}
		&:before {
			line-height: 22px;
			text-align: center;
			font-size: 14px;
			-webkit-font-smoothing: antialiased;
			-moz-osx-font-smoothing: grayscale;
			position: absolute;
			left: 0;
			right: 0;
			top: 0;
			bottom: 0;
			transition: color 0.3s;
		}
	}
	.box-content .wpoi-emails-list {
		.wpoi-emails-list-header {
			display: flex;
			padding: 0 30px;
			border-bottom: 1px solid $color--lily;
			background: $color--alabaster;
			.wpoi-list-fname,
			.wpoi-list-lname {
				flex: 1;
			}
			.wpoi-list-email {
				flex: 1;
				@include screen(small) {
					flex: 0 270px;
				}
			}
			> div {
				padding: 5px 0 7px;
				letter-spacing: -1px;
			}
			@media (max-width: 600px) {
				display: none;
			}
		}
		#wpoi-emails-list-content {
			height: 282px;
			overflow-x: hidden;
			overflow-y: auto;
			padding: 14px 30px 5px;
			border-bottom: 1px solid $color--lily;
			.wpoi-emails-list-subscriber {
				display: flex;
				.wpoi-list-fname,
				.wpoi-list-lname,
				.wpoi-list-email {
					overflow: hidden;
					letter-spacing: -0.65px;
					text-overflow: ellipsis;
					word-break: break-word;
					white-space: nowrap;
					&:before {
						@media (max-width: 600px) {
							content: attr(data-title) ":";
							margin-right: 4px;
							font-weight: bold;
						}
						@media (max-width: 500px) {
							display: block;
						}
					}
				}
				.wpoi-list-fname,
				.wpoi-list-lname {
					flex: 1;
				}
				.wpoi-list-email {
					flex: 1;
					@include screen(small) {
						flex: 0 270px;
					}
				}
				> div {
					padding: 5px 5px 5px 0;
					font-size: 12px;
					line-height: 14px;
					&.wpoi-list-email {
						padding-right: 0;
					}
				}
				&:not(:last-child) {
					@media (max-width: 600px) {
						margin-bottom: 10px;
						padding-bottom: 10px;
						border-bottom: 1px solid $color--lily;
					}
				}
				@media (max-width: 600px) {
					flex-direction: column;
				}
			}
		}
		.wpoi-emails-list-footer {
			padding: 15px 30px;
			.wph-button {
				width: 100%;
				display: block;
				margin: 0 auto;
				text-align: center;
				@include screen(small) {
					width: 94px;
					margin: 0 0 0 auto;
				}
			}
		}
	}
	&.show {
		opacity: 1;
		visibility: visible;
		.wpoi-complete-wrap {
			opacity: 1;
			transform: translate(-50%, -50%);
		}
	}
	@include screen(small) {
		left: 160px;
	}
}