.hustle-two .wpoi-css-selectors {
	display: block;
	margin-bottom: 18px;
	label {
		&:not(.wpoi-label-bold) {
			margin-bottom: 15px;
			padding: 0;
			color: $color--montana;
			font: 400 15px/18px $font;
		}
	}
	&-wrap {
		display: block;
		text-align: left;
		a {
			display: inline-block;
			margin-right: 2px;
			margin-bottom: 4px;
			padding: 0 5px;
			border-radius: 3px;
			background: $color--bluebell;
			background: linear-gradient(to right, $color--shipcove, $color--avalon);
			box-shadow: inset 0 1px 0 rgba(130, 139, 201, 0.5);
			color: $color--white;
			font: 400 12px/21px $font--opn;
			&:last-child {
				margin-right: 0;
			}
		}
	}
}

.hustle-two #hustle_custom_css,
.hustle-two #optin_custom_css {
	height: 210px;
	&.ace-solarized-light {
		.ace_gutter {
			background: $color--shuttle;
			color: $color--white;
		}
		.ace_scroller {
			background: $color--gallery;
			color: $color--maurice;
		}
	}
}

.hustle-two .wpoi-css-button {
	display: block;
	margin-top: 20px;
	text-align: right;
	.button {
		border-radius: 0;
		background: $color--shuttle;
		&:hover:not(:focus):not(:active) {
			background: $color--fiord;
		}
	}
}