.hustle-two {
	figure {
		margin: 0;
	}
	img {
		width: 100%;
		height: auto;
		display: block;
	}
	a img {
		display: block;
	}
	.wph-howdy,
	.wph-okay {
		width: 130px;
		height: 130px;
		display: block;
		margin: 30px auto 0;
		@extend %sprite;
		background-position: -892px 0;
		@media (min-width: 48em) {
			width: 100%;
			max-width: 226px;
			height: 239px;
			margin: 0 auto;
		}
	}
	.wph-howdy {
		@media (min-width: 48em) {
			background-position: -388px 19px;
		}
		@media (min-width: 75em) {
			background-position: -371px 19px;
		}
	}
	.wph-okay {
		@media (min-width: 48em) {
			background-position: -653px 19px;
		}
		@media (min-width: 75em) {
			background-position: -628px 19px;
		}
	}
	.wph-img--soon {
		width: 130px;
		height: 130px;
		display: block;
		margin: 30px auto 0;
		@extend %sprite;
		background-position: -892px 0;
		+ h6 {
			margin: 0 !important;
			padding: 30px 15px;
		}
	}
}

.hustle-two .wph-media {
	&--holder {
		width: 100%;
		height: 180px;
		position: relative;
		margin-bottom: 21px;
		border-radius: 3px;
		background-color: $color--flash;
		background-image: linear-gradient(45deg, $color--white 25%, transparent 25%, transparent 75%, $color--white 75%, $color--white), linear-gradient(45deg, white 25%, transparent 25%, transparent 75%, $color--white 75%, $color--white);
		background-size: 30px 30px;
		background-position: 0 0, 15px 15px;
	}
	&--preview {
		width: 100%;
		height: 100%;
		position: absolute;
		z-index: 1;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		border-radius: 3px;
		background-repeat: no-repeat;
		background-size: contain;
		background-position: center;
		box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.1);
	}
	&--options {
		z-index: 9;
		* {
			z-index: 99;
		}
		.wph-button--dots {
			top: 10px;
			position: absolute;
			right: 10px;
			padding: 7px 4px;
			border: 1px solid $color--bombay;
			border-radius: 4px;
			background: $color--white;
			.dot {
				width: 4px;
				height: 4px;
				float: left;
				display: block;
				vertical-align: middle;
				margin-right: 4px;
				border-radius: 50%;
				background: $color--gray;
				transition: 0.3s ease-in-out;
				&:last-child {
					margin-right: 0;
				}
			}
			&:hover .dot {
				background: $color--blue;
			}
		}
		&.hidden{
			display: none;
		}
	}
	&--list {
		min-width: 100%;
		position: absolute;
		top: 36px;
		.svg-triangle {
			position: absolute;
			top: -9px;
			right: 20px;
			z-index: 999;
			svg {
				width: 10px;
				height: 9px;
				margin: 0 auto;
				vertical-align: super;
			}
			polygon {
				fill: $color--white;
				stroke: $color--lilac;
				stroke-width: 1;
			}
		}
	}
	&--items {
		display: block;
		position: relative;
		margin: 0;
		padding: 17px;
		border: 1px solid $color--lilac;
		border-radius: 5px;
		background: $color--white;
		box-shadow: 0 0 5px 0 rgba(0,0,0,0.10);
		li {
			margin: 0;
			padding: 10px 0;
			border-bottom: 1px solid $color--lilac;
			&:last-child {
				padding-bottom: 0;
				border-bottom: 0;
			}
		}
		li, li a {
			color: #6D6D6D;
			font: 400 13px/16px $font;
			&:hover {
				color: $color--blue;
			}
			.dashicons {
				height: 14px;
				margin-right: 4px;
				font-size: 14px;
				line-height: 14px;
			}
		}
	}
	&--title {
		position: relative;
		span {
			display: block;
			padding-bottom: 10px;
			border-bottom: 1px solid $color--lilac;
			color: $color--dirt;
			font: bold 14px/16px $font--alt;
		}
		.wph-icon {
			cursor: pointer;
			position: absolute;
			top: 2px;
			right: 0;
			color: $color--iron;
			font-size: 12px;
			&:hover, &:active, &:focus {
				color: $color--dirt;
			}
		}
	}
	&--add {
		width: 100%;
		height: 100%;
		cursor: pointer;
		position: absolute;
		z-index: 2;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		border: 0;
		border-radius: inherit;
		outline: none;
		background: transparent;
		box-shadow: none;
		color: $color--montana;
		font: 400 15px/18px $font;
		text-transform: none;
		.dashicons {
			width: 30px;
			height: 30px;
			display: block;
			margin: 0 auto 10px;
			color: $color--casper;
			font-size: 30px;
			text-align: center;
			&:before {
				position: relative;
				top: -4px;
				left: 2px;
			}
		}
		&:hover {
			background: transparent;
			color: $color--montana;
		}
	}
}