.hustle-two {
	.switch-head {
		display: block;
		position: relative;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
		.toggle {
			position: absolute;
			top: -2px;
			left: 0;
		}
		> label {
			cursor: text;
			overflow: hidden;
			display: block;
			padding-left: 44px;
			line-height: 20px;
			letter-spacing: 0px;
			text-overflow: ellipsis;
			white-space: nowrap;
			> span:nth-child(1) {
				margin-right: 48px;
			}
			> span:nth-child(2) {
				opacity: 0.4;
				cursor: default;
				color: $color--shaft;
				font-size: 14px;
				font-family: $font--alt;
				&:before {
					content: $i-eye;
					margin-right: 7px;
					font-size: 12px;
					font-family: $font--icon;
				}
			}
		}
	}
	.switch-wrap {
		width: 100%;
		height: 0px;
		padding-top: 9px;
		border-bottom: 1px solid $color--lilac;
		&:not(.open) {
			overflow: hidden;
		}
		&.open {
			height: auto;
			padding-bottom: 30px;
		}
	}
	.switch-content {
		width: 100%;
		padding: 0 29px 30px 30px;
		border-radius: 5px;
		background: $color--storm;
		p + .wph-conditions {
			margin-bottom: 1px;
			padding-bottom: 30px;
			border-bottom: 1px solid $color--lilac;
		}
		.tabs .tabs-header {
			margin-top: 0;
			margin-bottom: 28px;
		}
		.tabs-header li {
			min-width: 79px;
			padding: 0;
			label {
				padding-right: 0;
				padding-left: 0;
			}
		}
		.wph-label--radio,
		.wph-label--checkbox {
			margin-bottom: 7px;
			> label {
				display: inline-block;
				padding-top: 0;
				padding-left: 29px;
				letter-spacing: 0px;
			}
			&:last-child {
				margin-bottom: 0;
			}
		}
	}
}