%color-edit {
	#wph-optin--colors {
		border-bottom: 1px solid $color--lilac;
		> div:first-child {
			position: relative;
			@extend %left-column;
		}
		> div:last-child {
			@extend %right-column;
		}
		@include screen(small) {
			margin-bottom: 15px;
			padding-bottom: 40px;
		}
	}
	#wph-optin--palette {
		.select2-container {
			width: 100% !important;
			max-width: 258px;
			margin-right: 27px;
			.wpmui-select2.select2-selection--single .select2-selection__rendered,
			.wpmui-select2.select2-selection--multiple .select2-selection__rendered {
				color: $color--wedgewood;
			}
			+ .wph-label--checkbox {
				display: inline-block;
				position: relative;
				top: 1px;
				> label {
					padding-top: 0;
					padding-left: 29px;
					letter-spacing: 0px;
				}
			}
		}
	}
	#optwiz-custom_color {
		margin-top: 20px;
		padding: 1px 15px 30px;
		border-radius: 5px;
		background: $color--storm;
		h4 {
			padding: 0 15px;
			line-height: 68px;
		}
		.wph-label--alt {
			margin: 0 0 5px;
			letter-spacing: 0px;
		}
		.row {
			position: relative;
			padding-top: 0;
		}
		.ocp-basic-other,
		.ocp-form-extras {
			padding-bottom: 1px;
		}
		.ocp-basic-content {
			z-index: 8;
		}
		.ocp-basic-other {
			z-index: 7;
		}
		.ocp-form-submit {
			z-index: 6;
		}
		.ocp-form-fields {
			z-index: 5;
		}
		.ocp-form-mailchimp {
			z-index: 4;
		}
		.ocp-form-extras {
			z-index: 3;
		}
		.ocp-additional-success {
			z-index: 2;
		}
		.ocp-additional-overlay {
			z-index: 1;
		}
	}
}