%settings-enews {
	#enews-sync-box {
		h6 {
			margin: 20px 0 8px;
			letter-spacing: -0.05px;
			&.alt {
				margin: 20px 0;
				text-align: center;
				@include screen(small) {
					margin: 30px 0 34px;
				}
			}
			@include screen(small) {
				margin: 30px 0 12px;
			}
		}
		.wph-table {
			margin-bottom: 16px;
			thead {
				display: none;
				tr th {
					line-height: 30px;
					&.wph-enews--setup,
					&.wph-enews--sync {
						text-align: center;
					}
				}
				@include screen(small) {
					display: table-header-group;
				}
			}
			tbody tr {
				&.wph-enews--editable {
					position: relative;
					padding-bottom: 64px;
					&:hover {
						.wph-button {
							@include screen(medium) {
								opacity: 1;
								visibility: visible;
							}
						}
					}
					&:last-child {
						padding-bottom: 49px;
						td.wph-enews--edit {
							bottom: 0;
							@include screen(small) {
								bottom: auto;
							}
						}
						@include screen(small) {
							padding-bottom: 0;
						}
					}
					@include screen(small) {
						padding-bottom: 0;
					}
				}
				td {
					padding: 12px 0 14px;
					line-height: 26px;
					letter-spacing: 0.4px;
				}
				td.wph-enews--edit {
					width: 100%;
					.wph-button {
						@include screen(medium) {
							opacity: 0;
							visibility: hidden;
						}
					}
					@include screen(small) {
						width: 70px;
						padding-right: 15px;
					}
				}
				td.wph-enews--list,
				td.wph-enews--sync {
					margin: 0 0 15px;
					&:before {
						content: attr(data-title) ":";
						display: inline-block;
						margin-right: 5px;
						color: $color--shaft;
						font-family: $font--alt;
						font-weight: 700;
						text-transform: uppercase;
						@include screen(small) {
							display: none;
						}
					}
					&:last-child {
						margin-bottom: 0;
					}
					@include screen(small) {
						margin: 0;
					}
				}
				td.wph-enews--setup,
				td.wph-enews--sync {
					width: 100%;
					@include screen(small) {
						width: 55px;
					}
				}
				td.wph-enews--sync {
					&:before {
						position: relative;
						top: 2px;
						line-height: 23px;
					}
					.toggle {
						width: 33px;
					}
				}
			}
			tfoot tr {
				td {
					border-top: 0;
					&.wph-enews--savesetup {
						text-align: right;
					}
				}
			}
		}
		.wph-table--alt {
			margin-bottom: 30px;
			thead {
				display: block;
				tr th {
					line-height: 27px;
					text-transform: none;
				}
				@include screen(small) {
					display: table-header-group;
				}
			}
			tbody {
				tr:first-child td {
					padding: 15px 0 0;
					.wph-input--checkbox {
						top: 17px;
						left: 1px;
					}
				}
				tr td {
					padding: 10px 0 0;
					label:not(.wph-icon) {
						letter-spacing: 0.05px;
					}
					.wph-input--checkbox {
						display: block;
						position: absolute;
						top: 12px;
						left: 1px;
					}
				}
				tr:not(:first-child) td {
					border-top: 0;
				}
			}
		}
	}
}