@import "admin/templates/optins/_tpl-emails.scss";
@import "admin/templates/optins/_tpl-services.scss";
@import "admin/templates/optins/_tpl-content.scss";
@import "admin/templates/optins/_tpl-structure.scss";
@import "admin/templates/optins/_tpl-color.scss";
@import "admin/templates/optins/_tpl-shape.scss";
@import "admin/templates/optins/_tpl-css.scss";
@import "admin/templates/optins/_tpl-settings.scss";

%left-column {
	&.col-xs-12 {
		padding-left: 0;
	}
	&.col-lg-3 {
		@include screen(large) {
			max-width: 21%;
			flex-basis: 21%;
		}
	}
}

%right-column {
	&.col-xs-12 {
		padding-right: 0;
		padding-left: 17px;
	}
	&.col-lg-9 {
		@include screen(large) {
			max-width: 79%;
			flex-basis: 79%;
		}
	}
}

#wpoi-emails-list-modal {
	// Modal: View Emails List
	@extend %modal-emails;
}

#wpoi-wizard {
	.box .box-title {
		h3 {
			line-height: 59px;
		}
	}
	.box .box-content {
		padding: 0;
	}
	// Basic Setup
	@extend %basic-setup;
	// Content & Design
	@extend %content-edit;
	@extend %structure-edit;
	@extend %color-edit;
	@extend %shape-edit;
	@extend %css-edit;
	// Display Settings
	@extend %display-settings;
}